<template>
  <li class="pl-4">
    <a
        :title="label"
        :href=`href`
        class="nav-menu__item hover:text-brand-dark-accent"
        :class="{ 'active text-brand-primary' : active }"
    >{{ ucLabel }}</a>
  </li>
</template>

<script>
export default {
  props: ['label'],
  data() {
    return {
      href: `/${this.label}`
    }
  },
  computed: {
    active() {
      return window.location.pathname.split('/').filter(i => i === this.label).length > 0
    },
    ucLabel() {
      let ucFirst = this.label.toUpperCase()[0]
      return `${ucFirst}${this.label.splice(1)}`
    }
  },

  created() {
    console.log({
      mobileActive: this.active,
      ucLabel: this.ucLabel,
      href: this.href
    })
  }
}
</script>

