<template>
  <a :title="title" :href="href"
     class="ml-6 text-gray-500 hover:text-brand-dark-accent"
     :class="{ 'active text-brand-primary': active }">
    {{ label }}
  </a>
</template>

<script>

export default {
  props: ['label', 'href'],

  data() {
    return {
      settings: null,
      active: this.isActive()
    }
  },

  computed: {
    title() {
      return `JHavens.tech ${this.label} Page`
    }
  },

  methods: {
    isActive() {
      let path = window.location.pathname
      if (path === '' || path === '/') {
        return this.href === '/'
      }
      return path.split('/').filter(segment => segment === this.label.toLowerCase()).length > 0
    }
  },

  created() {
    axios('/metadata.json')
        .then(res => this.settings = res)
        .catch(err => console.log(err))
  }

}
</script>